"use client";
import { Spin } from "antd";
import axios from "axios";
import { useEffect, useState } from "react";

type ResType = {
  id: number;
  title: string;
  userId: number;
  completed: boolean;
};

function useFetchData(url: string) {
  const [data, setData] = useState<ResType[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  useEffect(() => {
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setData(data);
      })
      .catch((er) => {
        console.log(er);
      })
      .finally(() => {
        setLoading(true);
      });
  }, []);
  return { data, loading };
}
function TodoListPage() {
  const { data, loading } = useFetchData(
    `https://jsonplaceholder.typicode.com/todos`
  );

  return (
    <div>
      <h1 className="text-2xl font-bold ">TodoListPage</h1>
      <ul className="grid grid-cols-5 gap-2 ">
     
        {data.map((i) => {
          return (
            <li
              key={i.id}
              className="border border-slate-300 rounded-xl hover:scale-105 transition-all 0.5s px-2 py-1 text-md font-bold cursor-pointer"
            >
              <span>{i.title}</span>
            </li>
          );
        })}
      </ul>
    </div>
  );
}

export default TodoListPage;
